import React, { useContext } from "react";
import {
  Row,
  Col,
  Card,
  CardTitle,
  //   CardText,
  // CardDeck,
  // CardSubtitle,
  CardBody,
  Button,
} from "reactstrap";

import { CartContext } from "./CartContext";

export const CartSummary = () => {
  const [cart, setCart] = useContext(CartContext);
  const totalPrice = cart.reduce(
    (acc, curr) =>
      acc + parseFloat(curr.price.replace("$", "") * curr.quantity),
    0
  );
  const totalQuantity = cart.reduce((acc, curr) => acc + curr.quantity, 0);

  return (
    <Card className="mb-4">
      <CardBody>
        <Row>
          <Col>
            <CardTitle tag="h5">Cart Summary</CardTitle>
          </Col>
          <Col className="text-center">
            <h6>items in cart: {totalQuantity}</h6>
          </Col>
          <Col className="text-center">
            <h6>total price: {totalPrice.toFixed(2)}</h6>
          </Col>
        </Row>
      </CardBody>
    </Card>
  );
};
export const handleCartClick = () => {
  document.querySelector(".cart").classList.toggle("d-none");
  document.querySelector(".shop").classList.toggle("d-none");
};

export const Cart = () => {
  const [cart, setCart] = useContext(CartContext);
  const updateCartPlus = (props) => {
    let match = cart.find((e) => e.code === props);

    match.quantity++;

    setCart(
      [...cart.filter((product) => product.code !== props), match].sort(
        (a, b) => {
          if (a.name < b.name) {
            return -1;
          } else if (a.name > b.name) {
            return 1;
          } else return 0;
        }
      )
    );
  };
  const updateCartMinus = (props) => {
    let match = cart.find((e) => e.code === props);

    if (match.quantity > 0) {
      match.quantity--;
    }

    setCart(
      [...cart.filter((product) => product.code !== props), match].sort(
        (a, b) => {
          if (a.name < b.name) {
            return -1;
          } else if (a.name > b.name) {
            return 1;
          } else return 0;
        }
      )
    );
  };
  const updateCartRemove = (props) => {
    setCart(cart.filter((product) => product.code !== props));
  };

  const cartContent = cart.map((shoppingList, index) => {
    return (
      <Row key={index + shoppingList.code}>
        <Col>{shoppingList.name}</Col>
        <Col>{shoppingList.price}</Col>
        <Col>
          {shoppingList.quantity}
          <span
            className="add "
            onClick={() => updateCartPlus(shoppingList.code)}
          >
            {" "}
            <i className="fa fa-plus"></i>{" "}
          </span>
          <span
            className="subtract"
            onClick={() => updateCartMinus(shoppingList.code)}
          >
            {" "}
            <i className="fa fa-minus"></i>{" "}
          </span>
          <span
            className="remove"
            onClick={() => updateCartRemove(shoppingList.code)}
          >
            {" "}
            <i className="fa fa-times"></i>{" "}
          </span>
        </Col>
        <Col>
          {(
            parseFloat(shoppingList.price.replace("$", "")) *
            shoppingList.quantity
          ).toFixed(2)}
        </Col>
      </Row>
    );
  });
  const button = cart.length ? (
    <Button
      className="w-100 mt-4"
      color="primary"
      onClick={() => console.log("Checkout")}
    >
      Check Out
    </Button>
  ) : (
    ""
  );

  return (
    <Card>
      <CardBody>
        <CardTitle>Shopping Cart</CardTitle>
        {cartContent}
        {button};
      </CardBody>
    </Card>
  );
};
