import React, { useContext } from "react";
import {
  Col,
  Card,
  Button,
  CardImg,
  CardTitle,
  // CardText,
  // CardDeck,
  // CardSubtitle,
  CardBody,
} from "reactstrap";

import { CartContext } from "./CartContext";

const ProductCard = (props) => {
  const [cart, setCart] = useContext(CartContext);

  const addToCart = () => {
    let match = cart.find((e) => e.code === props.product.code);

    const product = {
      code: props.product.code,
      name: props.product.name,
      price: props.product.price,
      quantity: match ? match.quantity + 1 : 1,
    };
    match
      ? setCart(
          [
            ...cart.filter((product) => product.code !== match.code),
            product,
          ].sort((a, b) => {
            if (a.name < b.name) {
              return -1;
            } else if (a.name > b.name) {
              return 1;
            } else return 0;
          })
        )
      : setCart((currentState) =>
          [...currentState, product].sort((a, b) => {
            if (a.name < b.name) {
              return -1;
            } else if (a.name > b.name) {
              return 1;
            } else return 0;
          })
        );
  };

  return (
    <Col className={props.colWidth} key={props.product.code}>
      <Card>
        <CardImg
          top
          width="100%"
          src={props.product.image}
          alt="Card image cap"
        />
        <CardBody className="text-center">
          <CardTitle tag="h5">{props.product.name}</CardTitle>
          <div>Price: {props.product.price}</div>
          <div className="mb-3">Code: {props.product.code}</div>
          <Button className="w-100" color="primary" onClick={addToCart}>
            Add to Cart
          </Button>
        </CardBody>
      </Card>
    </Col>
  );
};

export default ProductCard;
